import './fullPageScroll.scss';
import './scrollAnimation.scss';
import { useEffect, useRef, useState } from 'react';
import Info1 from '../Infos/Info1';
import Info2 from '../Infos/Info2';
import Info3 from '../Infos/Info3';

const FullPageScroll = () => {
  //          
  const [scrollOffset, setScrollOffset] = useState({offset:0});
    
  const wrapperRef = useRef();
  useEffect(() => {
    const wheelHandler = (e) => {
      e.preventDefault();
      //    
      const { deltaY } = e; //  wheel    ,   
      const { scrollTop } = wrapperRef.current; //     
      const pageHeight = window.innerHeight;  //     100vh 
      
      if (deltaY > 0) {
        //    
        //   1
        if (scrollTop >= 0 && scrollTop < pageHeight) {
          //  2 
          wrapperRef.current.scrollTo({
            top: pageHeight,
            left: 0,
            behavior: "smooth",
          });
          setScrollOffset({
            ...scrollOffset,
            offset: pageHeight
          });
        }
        //   2
        else if (scrollTop >= pageHeight && scrollTop < pageHeight * 2) {
          //  3 
          wrapperRef.current.scrollTo({
            top: pageHeight * 2,
            left: 0,
            behavior: "smooth",
          });
          setScrollOffset({
            ...scrollOffset,
            offset: pageHeight * 2
          });
        }
        //   3
        else {
          //  3 
          wrapperRef.current.scrollTo({
            top: pageHeight * 2,
            left: 0,
            behavior: "smooth",
          });
        }
      }
      else {
        //    
        //   1
        if (scrollTop >= 0 && scrollTop < pageHeight) {
          //  1 
          wrapperRef.current.scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth",
          });
        }
        //   2
        else if (scrollTop >= pageHeight && scrollTop < pageHeight * 2) {
          //  1 
          wrapperRef.current.scrollTo({
            top: 0,
            left: 0,
            behavior: "smooth",
          });
          setScrollOffset({
            ...scrollOffset,
            offset: 0
          });
        }
        //   3
        else {
          //  2 
          wrapperRef.current.scrollTo({
            top: pageHeight,
            left: 0,
            behavior: "smooth",
          });
          setScrollOffset({
            ...scrollOffset,
            offset: pageHeight
          });
        }
      }
    };

    const wrapperRefCurrent = wrapperRef.current;
    wrapperRefCurrent.addEventListener("wheel", wheelHandler);
    return () => {
      wrapperRefCurrent.removeEventListener("wheel", wheelHandler);
    };
  }, []);
  return (
    <div ref={wrapperRef} className="inner_wrapper bg-bl">
      {/*  className   
       window.innerHeight    */}
      <div className="inner">
        <Info1 act={(scrollOffset.offset === 0 ? 1 : 0)}/>
      </div>
      <div className="inner">
        <Info2 act={(scrollOffset.offset === window.innerHeight ? 1 : 0)}/>
      </div>
      <div className="inner">
        <Info3 act={(scrollOffset.offset === window.innerHeight * 2 ? 1 : 0)}/>
      </div>
      
      
      
    </div>
  );
};

export default FullPageScroll;